<template>
	<view>
		<Head :tab="true" title='选择语言' />
		<view class="box">
			<view :class="[item.active ? 'list active' : 'list']" v-for="item in list" 
			:key="item.code"
			@click="TICKFn(item.code)"
			>
				<view><img :src="item.imgurl" alt="">
					<text>{{item.name}}</text>
				</view>
				<img src="../../../../static/img/dagou.png" v-if="item.active" alt="" class='Tick'>
			</view>
			<!-- <view class="list">
				<view><img src="../../../../static/img/Property 1=Chinese.png" alt="">
				<text>英文</text></view>
				<img src="../../../../static/img/dagou.png" alt="" class='Tick'>
			</view>
			<view class="list">
				<view><img src="../../../../static/img/Property 1=Chinese.png" alt="">
				<text>日文</text></view>
				<img src="../../../../static/img/dagou.png" alt="" class='Tick'>
			</view> -->
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list: [
					{name: '中文',
						imgurl: require('../../../../static/img/Property 1=Chinese.png'),
						active: true,
						code: '01'
					},
					{
						name: '英文',
						imgurl: require('../../../../static/img/Property 1=English.png'),
						active: false,
						code: '02'
					},
					{
						name: '日文',
						imgurl: require('../../../../static/img/riben.png'),
						active: false,
						code: '03'
					}
				]
			}
		},
		methods:{
			TICKFn(code){
				this.list.forEach(item=>{
					if(item.code == code){
						item.active = true;
					}else{
						item.active = false
					}
				})
			}
		}
	}
</script>

<style lang="less">
	.box {
		padding: 0 40rpx;
		box-sizing: border-box;
		margin-top: 48rpx;

		.list {
			height: 106rpx;
			background: #FFFFFF;
			border-radius: 32rpx;
			margin-bottom: 40rpx;
			padding: 29rpx 40rpx;
			box-sizing: border-box;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;

			view {
				display: flex;
				flex-direction: row;
				align-items: center;
			}

			img {
				width: 48rpx;
				height: 48rpx;
			}

			text {
				font-size: 28rpx;
				font-family: Source Han Sans CN;
				font-weight: 400;
				color: #333333;
				opacity: 1;
				margin-left: 24rpx;
			}

			.Tick {
				width: 34rpx;
				height: 23rpx;
				opacity: 1;
				display: inline-block;
				float: right;
			}
		}

		.active {
			border: 1px solid #0F172C;
		}
	}
</style>
